import React, { useState } from 'react';
import UsageBanner from '@/components/dashboard/UsageBanner';
import SessionCard from '@/components/dashboard/SessionCard';
import UsageStats from '@/components/dashboard/UsageStats';
import RecentActivity from '@/components/dashboard/RecentActivity';
import ProTip from '@/components/dashboard/ProTip';
import { useQuery } from '@tanstack/react-query';
import { getUserSessionUsage, SessionType } from '@/services/sessionService';
import { toast } from 'sonner';
import PageLayout from '@/components/layout/PageLayout';
import { useUser } from '@/hooks/useUser';

const DashboardPage: React.FC = () => {
  const [loadingType, setLoadingType] = useState<SessionType | null>(null);
  const { user } = useUser();

  const { data: usageData } = useQuery({
    queryKey: ['sessionUsage'],
    queryFn: getUserSessionUsage,
  });

  const isNearLimit = usageData && (
    usageData.quick.used >= usageData.quick.total * 0.8 || 
    usageData.extended.used >= usageData.extended.total * 0.8 || 
    usageData.premium.used >= usageData.premium.total * 0.8
  );

  const handleStartSession = async (type: SessionType) => {
    setLoadingType(type);
    try {
      const response = await fetch(`https://session.cartunlock.com/create-session`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          user_id: user?.id,
          site: type,
        }),
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.error || 'Erro ao iniciar sesso');
      }

      window.open(data.session_url, '_blank');
      toast.success('Sesso iniciada com sucesso!');
    } catch (error) {
      toast.error('Falha ao iniciar sesso');
      console.error(error);
    } finally {
      setLoadingType(null);
    }
  };

  return (
    <PageLayout>
      <UsageBanner showBanner={isNearLimit} />

      <div className="grid gap-6 grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 auto-rows-fr">
        <SessionCard
          type="quick"
          title="Quick Session"
          description="1-hour US shopping access"
          hours={1}
          tooltipContent="Perfect for quick purchases. Gives you 1 hour of US IP access."
          onClick={() => handleStartSession('quick')}
          disabled={loadingType === 'quick'}
        />
        <SessionCard
          type="extended"
          title="Extended Session"
          description="4-hour US shopping access"
          hours={4}
          isPopular={true}
          tooltipContent="Our most popular option. Great for browsing multiple sites."
          onClick={() => handleStartSession('extended')}
          disabled={loadingType === 'extended'}
        />
        <SessionCard
          type="premium"
          title="Premium Session"
          description="8-hour US shopping access"
          hours={8}
          tooltipContent="Enhanced protection with dedicated IP. Ideal for secure transactions."
          onClick={() => handleStartSession('premium')}
          disabled={loadingType === 'premium'}
        />
      </div>

      <div className="mt-12">
        <h2 className="text-xl font-semibold mb-4">Account Overview</h2>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <UsageStats />
          <RecentActivity />
        </div>
      </div>

      <div className="mt-8">
        <ProTip />
      </div>
    </PageLayout>
  );
};

export default DashboardPage;