import { useEffect } from "react"
import { useSearchParams, useNavigate } from "react-router-dom"
import { Loader2 } from "lucide-react"
import { toast } from "@/hooks/use-toast"

const LoadingSessionPage = () => {
  const [params] = useSearchParams()
  const navigate = useNavigate()

  useEffect(() => {
    const slug = params.get("slug")
    if (!slug) return navigate("/")

    const fetchSession = async () => {
      try {
        const response = await fetch(`${import.meta.env.VITE_SUPABASE_REST_URL}/sessions?slug=eq.${slug}`, {
          headers: {
            apikey: import.meta.env.VITE_SUPABASE_ANON_KEY!,
            Authorization: `Bearer ${import.meta.env.VITE_SUPABASE_ANON_KEY!}`,
          },
        })

        const data = await response.json()
        const session = data?.[0]

        if (session?.session_url) {
          window.location.href = session.session_url
        } else {
          toast({ title: "Erro", description: "Sesso no encontrada.", variant: "destructive" })
          navigate("/")
        }
      } catch (error) {
        toast({ title: "Erro", description: "Erro ao buscar sesso", variant: "destructive" })
        navigate("/")
      }
    }

    fetchSession()
  }, [params, navigate])

  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-cartunlock-background text-white">
      <Loader2 className="animate-spin w-12 h-12 mb-6 text-cartunlock-blue" />
      <h1 className="text-2xl font-bold">Preparando sua sesso...</h1>
      <p className="text-muted-foreground mt-2">Aguarde enquanto redirecionamos</p>
    </div>
  )
}

export default LoadingSessionPage
