import { useState, useEffect } from "react";
import { supabase } from "@/integrations/supabase/client";

export interface User {
  id: string;
  email?: string;
  name?: string;
}

export const useUser = () => {
  const [user, setUser] = useState<User | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const fetchUser = async () => {
      try {
        const { data, error } = await supabase.auth.getUser();
        if (data?.user) {
          const { id, email } = data.user;
          setUser({ id, email });
        } else {
          console.warn("Usurio no encontrado:", error?.message);
        }
      } catch (err) {
        console.error("Erro ao buscar usurio:", err);
      } finally {
        setIsLoading(false);
      }
    };

    fetchUser();
  }, []);

  const login = (userData: User) => {
    setUser(userData);
    localStorage.setItem("cartunlock_user", JSON.stringify(userData));
  };

  const logout = () => {
    setUser(null);
    localStorage.removeItem("cartunlock_user");
  };

  return {
    user,
    isLoading,
    login,
    logout,
    isLoggedIn: !!user,
  };
};